import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import AlertTitle from '@mui/material/AlertTitle';

const dispatchedEvents = [];

const SrsMuiAlertTitle = function (props, ref) {
  let componentProps = {...props, theme: props.muiTheme};
  return (
    <AlertTitle {...componentProps}>
      <slot />
    </AlertTitle>
  );
}

const styles = `
  /* Custom styles for the AlertTitle component */
`;

const props = [
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiAlertTitle), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
